import React from 'react';
import TextField from "@material-ui/core/TextField";
import Autocomplete from "@material-ui/lab/Autocomplete";
import { makeStyles } from "@material-ui/core/styles";
import { getVentasByRoom, sortVentas } from '../utils/UtilsVenta';

const SearchBoxRooms = (props) => {
  const style = {
    root: {
      overflow: "hidden",
      textOverflow: "ellipsis",
      whiteSpace: "nowrap",
      right: "52px"
    }
  };

  const useStyles = makeStyles(() => ({
    ...style
  }));
  const styleClasses = useStyles();
  return (
    <Autocomplete
      id="combo-box-product"
      options={props.rooms}
      getOptionLabel={option => option.numero}
      onChange={(event, value) => {
        props.onChangeRoom(value);
        console.log(value.numero);
        props.setFilasTabla(sortVentas(getVentasByRoom(value.numero, props?.ventas)));
      }}
      renderInput={params => (
        <TextField
          {...params}
          InputLabelProps={{ classes: styleClasses }}
          label={
            "Seleziona la stanza"
          }
          variant="outlined"
          fullWidth
        />
      )}
    />
  );
}

export default SearchBoxRooms;